import React, { Component, PureComponent } from 'react';
import appConfig from '../appConfig';
import Units from '../units_config.json';
import pdfConfig from '../pdfConfig.json'
let root_folder = Units.icons_root_url;
import Snap from 'snapsvg';

export default class Unit extends Component {
    constructor(props) {
        super(props);
        this.element;
        this.unit;
        this.name = '';
       
        this.state = {
            modelSvg: root_folder + props.name + '.svg'
        }
    
        this.planePartsClicked = this.planePartsClicked.bind(this)
    }

    svgRender() {
        this.element = Snap(this.svgDiv);
        let that = this;
        // that.element.removeData()
        Snap.load(that.state.modelSvg, function (data) {
            that.data = data;
            if (that.element) {
                that.element.append(data);
                let svgContainerWidth = $('.unit-container').width();
                let svgWidth = $('.unit-container svg').width();
                let svgHeight = $('.unit-container svg').height();
                let ratio = parseInt(svgWidth) / parseInt(svgHeight);
               
                let supposedContainerHeight = svgContainerWidth / ratio;

                let screenHeight = $(window).height() - $('.custom_navbar').height();
                if (supposedContainerHeight > screenHeight) {
                    svgContainerWidth = svgContainerWidth * ((screenHeight - 50) / supposedContainerHeight);
                    supposedContainerHeight = screenHeight - 50;
                }

                svgContainerWidth=svgContainerWidth>400?400:svgContainerWidth;
                supposedContainerHeight=supposedContainerHeight>450?450:supposedContainerHeight;
        $('.unit-container svg').attr('width', svgContainerWidth + 'px');
        $('.unit-container svg').attr('height', (supposedContainerHeight) + 'px');


                // that.element.removeData();
                // that.element.add(data);
                that.element.click(that.planePartsClicked);
                $('.unit-container text:eq(1)').css('font-weight', 'bold')

            }
        });
    }
    planePartsClicked(e) {
        let that = this;
        let target = e.target;
        console.log(target.tagName)
       
        if (target.tagName == 'use') {
            let selector= $(target).attr('id');
                console.log(typeof selector)
            // for TR button
                if (typeof(selector)!='undefined' && selector.includes('TR_')) {
                    let htmlPage = selector.split('_').pop();
                    let TRPage = pdfConfig.mainUrl + htmlPage + '.html';
                    window.open(TRPage, '_blank');
                    return;
                }

            selector = $(target).attr('xlink:href');
            console.log(selector)
            selector = selector.split('#')[1];
            // for info button
            if(selector.includes('info')){
                console.log('info button is clicking')
            }
            that.props.getAndUpdateUnit(selector)
        }

        else if (target.tagName == 'tspan') {
            let selector = $(target).text();

            let domainpdf = (selector.split(' ')[0]).toLowerCase();
            selector = selector.replace(/\s*$/, '');
            let pdfNum = selector.split(' ').pop();

            if (typeof (pdfConfig.pdfFiles[domainpdf]) == "undefined") {
                return;
            }
            let pdfUrl = pdfConfig.mainUrl + pdfConfig.pdfFiles[domainpdf] + pdfNum + '.pdf';
            window.open(pdfUrl, '_blank');
        }
       else if (target.tagName == "text") {
            let selector = $(target);
            let id = selector.attr('id');
            if(typeof(id)!='undefined'){
                that.props.getAndUpdateUnit(id);
            }
            else{
                let selector = $(target).text();

                let domainpdf = (selector.split(' ')[0]).toLowerCase();
                selector = selector.replace(/\s*$/, '');
                let pdfNum = selector.split(' ').pop();
    
                if (typeof (pdfConfig.pdfFiles[domainpdf]) == "undefined") {
                    return;
                }
                let pdfUrl = pdfConfig.mainUrl + pdfConfig.pdfFiles[domainpdf] + pdfNum + '.pdf';
                window.open(pdfUrl, '_blank');
            }
            
        }
    }

    componentWillMount(props) {

    }
    componentDidMount() {
        
            this.svgRender()
        
    }
    componentDidUpdate() {
    
            this.svgRender()
      
    }
    componentWillReceiveProps(props) {
        let cont=$(this.svgDiv);
        // cont.empty();
        // console.log(cont.children())
        this.setState({
            modelSvg: root_folder + props.name + '.svg'
        })
    }
    render() {
        return (
            <div className="text-center unit-container" ref={d => this.svgDiv = d} />
        )
    }
}