$( document ).ready(function() {
    var winwid = $( window ).width();
    var previousSelctedFunc;
    $( window ).resize(function() {
        winwid = $( window ).width();
        if (winwid >= 992) {
            $(".list-group").hide();
            var height = $(".listing-bars").height();
             height=height<540?550:height;
            $(".listing-bars .listing-list .bar").height(height);
            $('.funt-list').show()
            
        }

        if (winwid < 992) {
            $(".list-group").show();
            // $(".listing-bars .listing-list .bar").height('');
            // $('.funt-list').hide()
        }
        
      });
    if (winwid >= 992) {
        $(".list-group").hide();
        var height = $(".listing-bars").height();
        height=height<540?550:height;
        $(".listing-bars .listing-list .bar").height(height);
        $('.funt-list').show()
    }
    else {
        if (winwid < 992) {
            $(".list-group").show();
            // $(".listing-bars .listing-list .bar").height('');
            // $('.funt-list').hide()
        }
    }
    if (winwid <= 991) {
        $(".gnd_eng_sys").click(function () {
            // $("#GND_ENG_STOPPED ul").toggle();
            // $("#GND_ENG_STOPPED").toggle();

        });
    }


    // collapse on mobile

    $(document).delegate('.funt-head', 'click', function (e) {
        if (winwid < 992) {
            var selectedval = $(this).find('h4').text();
            if (previousSelctedFunc == selectedval) {
                previousSelctedFunc = '';
                $('.funt-list').hide();
                return;
            }
            previousSelctedFunc = selectedval;
            $('.funt-list').hide();
            $(this).siblings('.funt-list').show();
        }
    })

   
    $(".version").mouseover(function(){
        $(this).css("width", "105px");
    });
    $(".version").mouseout(function(){
        $(this).css("width", "95px");
    });
    $(".notice").mouseover(function(){
        $(this).css("width", "105px");
    });
    $(".notice").mouseout(function(){
        $(this).css("width", "95px");
    });
    $(".new").mouseover(function(){
        $(this).css("width", "105px");
    });
    $(".new").mouseout(function(){
        $(this).css("width", "95px");
    });




    $('body').delegate('.hamburger', 'click', function () {
        $(".click-mobile").toggle("slow");
    })
   
    $('body').delegate('.column-text','click',function(e){
     
        let base=appConfig.baseUrl;
        
        base=base+'#/cautions';
       
        window.location.href=base;
    })
});